import React, { useContext } from 'react';
import { CartContext } from '../../components/Context';
import Header from '../../components/header/Header';
import Footer from '../../components/footer/Footer';
import { Link, useParams } from 'react-router-dom';
import './tovar.css';
import toCart from './../../img/pictures/toCart.svg';
import inCart from './../../img/pictures/inCart.svg';

const Tovar = () => {
    const { items, onAddToCart, isItemInCart } = useContext(CartContext); //  
    const { title } = useParams(); //  title  URL

    const item = items.find(
        (item) => item.title.toLowerCase() === decodeURIComponent(title).toLowerCase()
    );

    const isAdded = item && isItemInCart(item.title); // ,       title

    if (!item) return <p>  </p>;

  return (
    <>
      <Header />
      <div className="container">
        {/*    */}
        <p className="product-title">{item.category}</p>
        <h1 className="product-title2">{item.title}</h1>
        <div className="preinfo">
          <p className="product-meta">: 21312132 | : 1 </p>
          <p className="product-rating">
            <img src="/img/icons/stars.png" alt="stars" />
            <span className="rating-text">(8 )</span>
          </p>
        </div>
        <section className="product-section">
          <div className="thumbnail-gallery">
            <img src={`/${item.imageUrl}`} alt="  1" className="thumbnail" />
            <img
              src={`/${item.imageUrl}`}
              alt="  2"
              className="thumbnail"
            />
          </div>
          <div className="product-gallery">
            <div className="img-main">
              <img src={`/${item.imageUrl}`} alt={item.title || ''} className="main-image" />
            </div>
          </div>

          <div className="product-details">
            <ul className="product-specs">
              <h2>
                <strong>:</strong>
              </h2>
              <li> : 6</li>
              <li> : H-S-S</li>
              <li> :  </li>
              <li> : </li>
              <li> : </li>
              <li>  : </li>
              <li>: </li>
              <li> : Superstrat</li>
              <li>, : 25.5</li>
              <li> : Tremolo</li>
              <a href="#" className="specs-link">
                 
              </a>
            </ul>

            <div className="order-block-tovar">
              <div className="buy-prod">
                <div className="subtitle2">
                  <span>{item.price} .</span>
                </div>

                <button className="buy">
                     {!isAdded ? (
                        <button onClick={() => onAddToCart(item)}>
                            <img className="img2" src={toCart} alt="Add to Cart" />
                        </button>
                     ) : (
                        <Link to="/cart">
                            <img className="img2" src={inCart} alt="In Cart" />
                        </Link>
                    )}
                </button>
              </div>
              <div className="aftertitle">
                <span> !</span>
              </div>
            </div>
          </div>
        </section>

        {/*  */}
        <section className="reviews-section">
          <h2></h2>
          <div className="review-summary">
            <div className="review-rating">
              <span className="review-score">4.7</span>
              <p>8 </p>
            </div>
            <p className="recommend">100% </p>
          </div>
          <div className="review-list">
            <article className="review-item">
              <p className="review-author">
                <strong> </strong>
              </p>
              <p className="review-text">
                      .    , 
                  ,      .  , 
                  .  .
              </p>
              <p className="review-date">28  2024</p>
              <img
                src="/img/icons/stars.png"
                alt="stars"
                className="main-image"
              />
            </article>
            <article className="review-item">
              <p className="review-author">
                <strong> </strong>
              </p>
              <p className="review-text">
                    .  ,    , 
                 ,   ""  .
              </p>
              <p className="review-date">15  2024</p>
              <img
                src="/img/icons/stars.png"
                alt="stars"
                className="main-image"
              />
            </article>
          </div>
        </section>
      </div>
      <Footer />
    </>
  );
};

export default Tovar;
