import React, { useState } from "react";
import "./registration.css";
import { Link } from "react-router-dom";
import Header from "../../components/header/Header";
import { fetchRegister, selectIsAuth } from "../../redux/slices/auth";
import { useDispatch, useSelector } from "react-redux";
import { Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";

export const Registration = () => {
  const isAuth = useSelector(selectIsAuth);
  const dispatch = useDispatch();

  //      
  const [showErrors, setShowErrors] = useState(false);

  const {
    register,
    handleSubmit,
    watch,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      fullName: "",
      email: "",
      password: "",
      confirmPassword: "",
      changeInput: "",
    },
    mode: "onSubmit", //     
  });

  const onSubmit = async (values) => {
    const data = await dispatch(fetchRegister(values));
    if (!data.payload) {
      return alert("  ");
    }
    if ("token" in data.payload) {
      window.localStorage.setItem("token", data.payload.token);
    }
  };

  if (isAuth) {
    return <Navigate to="/" />;
  }

  const password = watch("password");

  const handleFormSubmit = (data) => {
    setShowErrors(true); //     
    handleSubmit(onSubmit)(data); //   
  };

  return (
    <>
      <Header />
      <section className="Login">
        <div className="preview">
          <h3 className="text-center"></h3>
          <div className="registration-cssave">
            <form onSubmit={handleSubmit(handleFormSubmit)}>
              <div className="form-group">
                <input
                  className="form-control-item"
                  type="text"
                  placeholder=""
                  {...register("fullName", {
                    required: " ",
                    minLength: { value: 4, message: " 4 " },
                    maxLength: { value: 15, message: " 15 " },
                  })}
                />
                {showErrors && errors.fullName && <p className="check">{errors.fullName.message}</p>}
              </div>
              <div className="form-group">
                <input 
                  className="form-control-item"
                  type="email"
                  placeholder="Email"
                  {...register("email", { 
                    required: " ",
                    pattern: {
                      value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                      message: "  email",
                    },
                  })}
                />
                {showErrors && errors.email && <p className="check">{errors.email.message}</p>}
              </div>
              <div className="form-group">
                <input 
                  className="form-control-item"
                  type="password"
                  placeholder=""
                  {...register("password", {
                    required: " ",
                    minLength: { value: 6, message: " 6 " },
                  })}
                />
                {showErrors && errors.password && <p className="check">{errors.password.message}</p>}
              </div>
              <div className="form-group">
                <input
                  className="form-control-item"
                  type="password"
                  placeholder=" "
                  
                  {...register("confirmPassword", {
                    
                    required: " ",
                    validate: (value) =>
                      value === password || "  ",
                  })}
                />
                {showErrors && errors.confirmPassword && (
                  <p className="check">{errors.confirmPassword.message}</p>
                )}
              </div>
              <div className="form-group">
                <button
                  className="btn btn-primary btn-block create-account"
                  type="button"
                  onClick={handleFormSubmit} //     
                >
                  
                </button>
              </div>
              <div className="form-group">
                <Link to="/login">
                  <button className="log-in" type="button">
                     ? <a></a>
                  </button>
                </Link>
              </div>
            </form>
          </div>
        </div>
      </section>
    </>
  );
};

export default Registration;