import React, { useState } from "react";
import "./login.css";
import { Navigate } from "react-router-dom";
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import Header from '../../components/header/Header'
import { useForm } from 'react-hook-form'
import { fetchAuth, selectIsAuth } from "../../redux/slices/auth";

export const Login = () => {
  
  const isAuth = useSelector(selectIsAuth);
  const dispatch = useDispatch();
  const [redirectToAdmin, setRedirectToAdmin] = useState(false); //    
  const [redirectToHome, setRedirectToHome] = useState(false); //     
  const { 
    register,
    handleSubmit,
    formState: { errors, isValid },
  } = useForm({
    defaultValues: {
      email: '',
      password: ''
    },
    mode: 'onChange',
  });

  const [showErrors, setShowErrors] = useState(false);

  const onSubmit = async (values) => {
    //      admin
    if (values.email === 'admin@admin.com' && values.password === 'admin') {
      //      
      setRedirectToAdmin(true);
      return;
    }

    //       Redux
    const data = await dispatch(fetchAuth(values));

    if (!data.payload) {
      return alert('   ');
    }

    //   ,    localStorage
    if ('token' in data.payload) {
      window.localStorage.setItem('token', data.payload.token);
    }

    //          
    if (data.payload.role === 'admin') {
      setRedirectToAdmin(true);
    } else {
      setRedirectToHome(true);
    }
  };

  const handleFormSubmit = (data) => {
    setShowErrors(true); //     
    handleSubmit(onSubmit)(data); //   
  };

  //    ,   
  if (isAuth) {
    return <Navigate to="/" />;
  }

  //     
  if (redirectToAdmin) {
    return <Navigate to="/admin" />;
  }
  if (redirectToHome) {
    return <Navigate to="/" />;
  }

  return (
    <>
      <Header />
      <section className="Login">
        <div className="preview">
          <h3 className="text-center"></h3>
          <div className="registration-cssave">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="form-group">
                <input
                  className="form-control-item"
                  type="email"
                  placeholder="Email"
                  {...register("email", { 
                    required: " ",
                    pattern: {
                      value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                      message: "  email",
                    },
                  })}
                />
                {showErrors && errors.email && <p className="check">{errors.email.message}</p>}
              </div>
              <div className="form-group">
                <input
                  className="form-control-item"
                  type="password"
                  placeholder=""
                  {...register("password", {
                    required: " ",
                    minLength: { value: 5, message: " 6 " },
                  })}
                />
                {showErrors && errors.password && <p className="check">{errors.password.message}</p>}
              </div>
              <div className="form-group">
                <button className="btn btn-primary btn-block create-account" type="submit" onClick={handleFormSubmit}>
                  
                </button>
              </div>
              <div className="form-group">
                <Link to='/register'> 
                  <button className="register" type="submit"> ? <a></a></button>
                </Link>
              </div>
            </form>
          </div>
        </div>
      </section>
    </>
  );
};

export default Login;
