import React, { useContext, useState } from 'react';
import './admin.css';
import Header from './../../components/header/Header';
import Footer from '../../components/footer/Footer';
import { CartContext } from '../../components/Context';
import { useForm } from 'react-hook-form';

const Admin = () => {
  const { items, addProduct, updateProduct, deleteProduct } = useContext(CartContext);

  //   :   ,   
  const [imageUrl, setImageUrl] = useState('');
  const [updateImageUrl, setUpdateImageUrl] = useState('');

  //  
  const {
    register: addRegister,
    handleSubmit: handleAddSubmit,
    formState: { errors: addErrors },
    reset: resetAddForm,
  } = useForm({
    defaultValues: { category: '', title: '', price: 0 },
    mode: 'onChange',
  });

  //  
  const {
    register: updateRegister,
    handleSubmit: handleUpdateSubmit,
    formState: { errors: updateErrors },
    reset: resetUpdateForm,
  } = useForm({
    defaultValues: { id: '', category: '', title: '', price: 0 },
    mode: 'onChange',
  });

  //  
  const [deleteId, setDeleteId] = useState('');
  const [deleteError, setDeleteError] = useState('');

  //      
  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        alert('  ,    5 ');
        return;
      }
      
      const reader = new FileReader();
      reader.onloadend = () => {
        setImageUrl(reader.result); //  URL 
      };
      reader.readAsDataURL(file); //    URL
    }
  };

  //      
  const handleUpdateImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setUpdateImageUrl(reader.result); //  URL     
      };
      reader.readAsDataURL(file); //    URL
    }
  };

  //  
  const handleAdd = async (data) => {
    try {
      const productWithId = { ...data, id: Date.now(), imageUrl };
      await addProduct(productWithId);
      resetAddForm(); //    
      setImageUrl(''); //  URL 
    } catch (e) {
      console.error(e);
    }
  };

  //  
  const handleUpdate = async (data) => {
    if (!data.id) {
      setDeleteError(' ID   .');
      return;
    }

    try {
      const updatedProduct = {
        title: data.title,
        category: data.category,
        price: data.price,
        imageUrl: updateImageUrl || data.imageUrl,
      };

      await updateProduct(data.id, updatedProduct);
      resetUpdateForm(); //    
      setUpdateImageUrl(''); //  URL   
    } catch (e) {
      console.error(e);
    }
  };

  //  
  const handleDelete = async () => {
    if (!deleteId) {
      setDeleteError(' ID   .');
      return;
    }

    try {
      await deleteProduct(deleteId);
      setDeleteError(''); //     
    } catch (e) {
      setDeleteError('   .  ID.');
    }
  };

  return (
    <>
      <Header />
      <section className="admin">
        <div className="container">
          <h1 className="prei">-</h1>

          {/*   */}
          <div className="crud-section">
            <h2> </h2>
            <form onSubmit={handleAddSubmit(handleAdd)}>

              <input
                type="text"
                placeholder=""
                {...addRegister('title', { required: '  .' })}
                className="form-control-item"
              />
              {addErrors.title && <p className="check">{addErrors.title.message}</p>}

              <input
                type="text"
                placeholder=""
                {...addRegister('category', { required: '  .' })}
                className="form-control-item"
              />
              {addErrors.category && <p className="check">{addErrors.category.message}</p>}

              <input
                type="number"
                placeholder=""
                {...addRegister('price', {
                  required: '  .',
                  min: { value: 1, message: '    0.' },
                })}
                className="form-control-item"
              />
              {addErrors.price && <p className="check">{addErrors.price.message}</p>}

              {/*   */}
              <input
                type="file"
                onChange={handleImageUpload}
                className="form-control-item"
              />
              {imageUrl && <img src={imageUrl} alt="" width="100" />}
              {addErrors.image && <p className="check">{addErrors.image.message}</p>}

              <button type="submit" className="CRUD-btn">
                 
              </button>

              {/*  ,    */}
              {addErrors && Object.keys(addErrors).length > 0 && (
                <p className="check">,    .</p>
              )}
            </form>
          </div>

          {/*   */}
          <div className="crud-section">
            <h2> </h2>
            <form onSubmit={handleUpdateSubmit(handleUpdate)}>

              <input
                type="text"
                placeholder="ID "
                {...updateRegister('id', { required: ' ID   .' })}
                className="form-control-item"
              />
              {updateErrors.id && <p className="check">{updateErrors.id.message}</p>}

              <input
                type="text"
                placeholder=" "
                {...updateRegister('title', { required: '  .' })}
                className="form-control-item"
              />
              {updateErrors.title && <p className="check">{updateErrors.title.message}</p>}

              <input
                type="text"
                placeholder=""
                {...updateRegister('category')}
                className="form-control-item"
              />

              <input
                type="number"
                placeholder=" "
                {...updateRegister('price', {
                  min: { value: 1, message: '    0.' },
                })}
                className="form-control-item"
              />
              {updateErrors.price && <p className="check">{updateErrors.price.message}</p>}

              {/*    */}
              <input
                type="file"
                onChange={handleUpdateImageUpload}
                className="form-control-item"
              />
              {updateImageUrl && <img src={updateImageUrl} alt="" width="100" />}
              {updateErrors.image && <p className="check">{updateErrors.image.message}</p>}

              <button type="submit" className="CRUD-btn">
                 
              </button>
            </form>
          </div>

          {/*   */}
          <div className="crud-section">
            <h2> </h2>
            <input
              type="number"
              placeholder="ID   "
              value={deleteId}
              onChange={(e) => setDeleteId(e.target.value)}
              className="form-control-item"
            />
            {deleteError && <p className="check">{deleteError}</p>}
            <button className="CRUD-btn" onClick={handleDelete}>
               
            </button>
          </div>

          {/*   */}
          <div className="crud-section">
            <h2> </h2>
            {items.length > 0 ? (
              <table className="product-table">
                <thead>
                  <tr className="shapka">
                    <th>ID</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  {items.map((item) => (
                    <tr key={item.id}>
                      <td>{item.id}</td>
                      <td>{item.title}</td>
                      <td>{item.category}</td>
                      <td>{item.price} .</td>
                      <td>
                        <img
                          src={item.imageUrl}
                          alt={item.title}
                          width="50"
                          height="50"
                        />
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <p>  </p>
            )}
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Admin;
